import React, { useState, useEffect } from 'react';
import { Container, Card, Row, Col } from 'react-bootstrap';

function News() {
    const url = 'https://bing-news-search1.p.rapidapi.com/news?safeSearch=Off&textFormat=Raw';

    const [error, setError] = useState(null);
    const [isLoaded, setLoading] = useState(false);
    const [items, setItems] = useState([]);

    useEffect(() => {
        setLoading(true);
        fetch(url, {
            "method": "GET",
            "headers": {
                "x-bingapis-sdk": "true",
                "x-rapidapi-host": "bing-news-search1.p.rapidapi.com",
                "x-rapidapi-key": "c99acddff6mshc08508ac0ef4a76p1e33f0jsn8273c3819d11"
            }
        })
            .then(res => res.json())
            .then(
                (result) => {
                    setItems(result.value);
                    console.log(result.value[0].image.thumbnail.contentUrl);
                })
            .catch((err) => {
                setError(err);
            })
            .finally(() => {
                setLoading(false);
            });
    }, [])

    const renderCard = (item, key) => {
        return (
            <Col className="col-lg-3 d-flex align-items-stretch" key={key}>
                <Card border="light" className="myCard">
                    <Card.Img variant="top" className="cardImg" src={item.image.thumbnail.contentUrl} />
                    <Card.Body>
                        <Card.Title>{item.name}</Card.Title>
                        <Card.Text>
                            {item.description}
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className="text-muted cardFooter">{item.provider[0].name}</Card.Footer>
                </Card>
            </Col>
        )
    }

    if (error) {
        return <div>Error: {error.message}</div>;
    }

    if (isLoaded) {
        return <div>Loading...</div>;
    } else
        if (items) {
            return (
                <Container fluid>
                    <Row>
                        {items.map(renderCard)}
                    </Row>
                </Container >
            );
        }
}
export default News;