import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';

const myLogin = 'Admin';
const myPassword = '12345';

function Login(props) {
    const navigate = useNavigate();
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    function handleLogin(event) {
        event.preventDefault();
        const { login, password } = event.target.elements;

        if ((login.value === myLogin) && (password.value === myPassword)) {
            localStorage.setItem('login', 'true');
            props.login(true);
            console.log(props);
            navigate("/profile", { state: { isLogin: true } });
        }
        else {
            handleShow();
        }
    }

    return (
        <div className="login-form">
            <h1 className="text-info">News</h1>
            <h2 className="text-info">Log in</h2>
            <form onSubmit={handleLogin}>
                <div className="row mt-4">
                    <div className="col-2 text-left">
                        <label className="text-info" htmlFor="login">Login (Admin)</label>
                    </div>
                    <div className="col-10 text-right">
                        <input name="login" type="text" placeholder="Login" className="my-input" />
                    </div>
                </div>
                <div className="row mt-3">
                    <div className="col-2 text-left">
                        <label className="text-info" htmlFor="password">Password (12345) </label>
                    </div>
                    <div className="col-10 text-right">
                        <input name="password" type="password" placeholder="Password" className="my-input" />
                    </div>
                </div>
                <Button variant="outline-info mt-3 mr-5 mb-10" type="submit">Login
                </Button>
            </form>
            <Modal show={show} onHide={handleClose} size="sm" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton>
                    <Modal.Title>Помилка авторизації</Modal.Title>
                </Modal.Header>
                <Modal.Body>Ви невірно ввели логін або пароль!</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    );
}
export default Login;